.contact_us_container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-top: 2%;
  padding: 3%;
  padding-bottom: 8%;
  font-family: 'Yantramanav', sans-serif;
  color: #F1F1F1;
}

.info_container {
  grid-column: 1;
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  padding-top: 7%;
  padding-left: 7%;
}

.florida_address {
  grid-row: 1; 
}

.california_address {
  grid-row: 2;
  padding-top: 3%;
}

.contact_us_line {
  border-bottom: solid;
  border-width: 1%;
  border-color: #F1F1F1;
  width: 40%;
}

.contact_us_line2 {
  border-bottom: solid;
  border-width: 1%;
  border-color: #F1F1F1;
  width: 47%;
}

.state {
  font-size: 40px;
  font-weight: bold;
}

.address {
  font-size: 25px;
  padding-top: 1%;
  padding-bottom: 1%;
}

.street {
  font-size: 25px;
}

.email_phone_fax {
  grid-row: 3;
  font-size: 25px;
  padding-top: 5%;
}
